.playing-field {
    width: 20vw;
    height:90%;
    position: relative;
    
    display: flex;
    justify-content: center;
    align-items: center;


    border: 1px solid black;
    border-radius: 5rem;
    transition: all 0.5s;
    &:hover {

        transform: scale(1.1);
    }
}